.header{
    background-color: rgba(2, 16, 101, 0.91);
    width: 100%;
    min-height: 50px;
    display: flex;
    align-items: center;
    margin: 0 auto;
}
.header_wrapper{
    width: 70%;
    height: 100%;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;

    img{
        width: 40px;
        height: 35px;

    }

    .search{
        margin: 5px;
        width: 30%;
        min-width: 200px;
        display: flex;
        justify-content: center;
        align-items: center;

        input{
            border-radius: 15px 0px 0px 15px;
            padding: 15px;
            width: 100%;
            min-width: 150px;
            outline: none;
            height: 30px;
            border: none;
        }

        button{
            border-radius: 0px 15px 15px 0px;
            background-image: url(./loupe.png);
            height: 30px;
            min-width: 30px;
            background-position: center;
            background-size: 100%;
            border: none;
            background-color: rgb(220, 219, 219);

            &:hover{
                cursor: pointer;
            }
        }
    }
}