
.postsWrapper{
  width: 60%;           //60!
  min-width: 520px;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.block{
  border-radius: 10px;
}
.posts{
  width: 80%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.postImages{
  display: flex;
  flex-direction: row;
  justify-content: center;
}
.post{
  border-radius: 10px;
  box-shadow: 3px 3px 7px #b9b9b9;
  width: 100%;
  min-width: 520px;
  margin-bottom: 20px;
  padding: 20px;
  background-color: white;
  flex-direction: column;


  img{
    width: 230px;
    margin: 5px;
    border-radius: 5px;
  }
  p{
    margin: 10px 0px;
  }
  button{
    background-color: white;
    color: rgba(2, 16, 101, 0.91);
  }

}
.btnStyle{
  width: 80px;
  height: 30px;
  border: 2px solid rgba(2, 16, 101, 0.91);
  border-radius: 30px;

}

.newPost{
  width: 80%;
  min-width: 520px;
  margin: 20px;
  display: flex;
  flex-wrap: wrap;
  padding: 20px;
  background-color: white;
  box-shadow: 3px 3px 7px #b9b9b9;

  textarea{
    width: 100%;
    min-height: 100px;
    outline: none;
    margin-bottom: 5px;
    resize: none;
  }

  button{
    background-color: rgba(2, 16, 101, 0.91);
    color: white;

    &:hover{
      cursor: pointer;
      background-color: rgba(34, 54, 178, 0.99);
      border: none;
    }
  }
}

@media(max-width: 700px){
  .postsWrapper, .post, .newPost{
    min-width: 250px
  }
  .postImages{
    flex-wrap: wrap;
  }
}
