.navWrapper{
  width: 20%;
  min-width: 230px;
}
.nav{
  background-color: white;
  min-width: 230px;
  min-height: 600px;
  margin-top: 20px;
  padding: 20px 15px 0px 15px;
  border-radius: 10px;
  box-shadow: 3px 3px 7px #b9b9b9;
  margin-bottom: auto;

}
.userPhoto{
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 40px;
  text-align: center;

  img{
    width: 80%;
    margin-bottom: 30px;
  }
  h1{
    font-weight: 500;

  }
  span{
    margin-top: 10px;
    color: #5b5b5b;
    font-size: 18px;
  }
}

nav{
  a{
    display: block;
    padding: 5px 10px;
    margin: 10px;
    color: #393a3a;
    text-decoration: none;
    font-size: 20px;
    width: 85%;

    &:hover{
      background-color: #ecebeb;
      border-left: 7px solid rgba(2, 16, 101, 0.91);
      transition: .5s;
    }
  }
}