.leftEvent {
  width: 100%;
  display: block;
  min-width: 230px;

  @media (min-width: 1700px) {
    display: none;
  }
}

.rightEvent {
  width: 20%;
  min-width: 263px;
  display: none;

  @media (min-width: 1700px) {
    display: block;
  }
}

.events{
  height: 200px;
  border-radius: 10px;
  box-shadow: 3px 3px 7px #b9b9b9;
  margin-top: 20px;
  background-color: white;

  h2{
    color: grey;
    font-weight: 500;
    text-transform: uppercase ;
    padding: 10px;
  }

  img{
    width: 35px;
    height: 35px;
  }
}
.eventsItem{
  width: 95%;
  margin: 5px;
  display: flex;
  align-items: center;
  padding: 5px;

  span{
    margin-left: 10px;
    color: #484747;
  }

}